export const query = graphql`
    query{
        allContentsJson {
            edges {
                node {
                    menu {
                        es {
                            items {
                                title
                                url
                                id
                            }
                        }
                        en {
                            items {
                                title
                                url
                                id
                            }
                        }
                    }
                }
            }
        }
    }
`